<template>
  <div class="login-container">
      <!-- 登录框 -->
      <div class="login-box">
        <el-form ref="loginFormRef" :rules="loginRules" :model="loginForm">
           <div class="text">管理员账户</div>
           <el-col :span="16">
             <el-form-item prop="username" label-width="0">
               <el-input  placeholder="请输入用户名" v-model="loginForm.username" prefix-icon="el-icon-user"    clearable></el-input>
             </el-form-item>
           </el-col>
           <el-col :span="16">
              <el-form-item  prop="passwd" label-width="0">
                <!-- el-input 监听键盘按下状态得用@keyup.enter.native,如果是非el-input 组件，可以直接用@keyup.enter。 -->
                <el-input @keyup.enter.native="submitForm()" placeholder="请输入密码"  v-model="loginForm.passwd" prefix-icon="iconfont icon-mima" show-password clearable></el-input>
              </el-form-item>
           </el-col>
           <el-button type="primary" plain  @click="submitForm()">登录</el-button>
        </el-form>
      </div>

      <div class="meteor" style="transform: rotate(30deg);" v-for="(item,index) in 5" :key="index" ref="meteor">
        <div class="line"  :style="`animationDelay:${index*100}ms`"></div>
      </div>

<!-- <rain-component :rainNumber="20" :rotateDeg="-10" :w="2" :h="20"></rain-component> -->
  </div>
</template>

<script>
import '../assets/css/iconfont/iconfont.css'
import RainComponent from '../components/RainComponent.vue'
export default{
   components:{
     RainComponent
   },
   data(){
     return{
       loginForm:{
         username:'PJC',
         passwd:''
       },
       loginRules: {
         username: [
           { required: true, message: '请输入用户名', trigger: 'blur' },
           { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
         ],
         passwd: [
           { required: true, message: '请输入密码123456', trigger: 'blur' }
         ],

       }
     }
  },
  mounted() {
    this.randomMeteor();
  },
  methods:{
    submitForm() {
      this.$refs.loginFormRef.validate((valid) => {
        if (!valid) return;
        if(this.loginForm.passwd.toString()!='123456'){
          this.$message.error('密码错误，请检查！');
        } else {
         this.$message.success("登录成功！");
         this.$router.push('/fontpage');
        }
    });
    },
    randomMeteor(){
      let meteors=this.$refs['meteor'];
      meteors.forEach((item)=>{
        //screen.height
        item.style.top=(Math.floor(Math.random()*(screen.height-400)))+ "px";
        item.style.left=(Math.floor(Math.random()*screen.width)-2)+ "px";
      })
    }
  }

}
</script>

<style lang="less" scoped>
.login-container{
    height: 100%;  position: relative;  overflow: hidden;
    background: url(https://www.17sucai.com/preview/806169/2020-11-24/%E7%99%BB%E9%99%86/image/bg1.jpg) no-repeat;
    background-size: 100% 100%;
    // background-size: cover;  // 把背景图像扩展至足够大，以使背景图像完全覆盖背景区域。
    display: flex; align-items: center; justify-content: center;
    .login-box{
        width: 500px; height: 400px;
        border-radius: 5px; background:rgba(0,0,0,0.2);box-shadow: -10px 10px 10px #223955;
    }
    .el-form{
      height: 100%;
      display: flex;  flex-flow:column; justify-content: space-around; align-items: center;
      .text{
        font-size: 25px;  color:#D3D7F7;
      }

    }
}

// 流星
.meteor{
  width:2px; height: 200px;  position: absolute;
  .line{
    width: 2px; height: 200px;   border-radius: 2px; position: absolute;
     background: linear-gradient(to top,rgb(2, 161, 195, 1),rgba(11, 36, 66, 0.1));
     box-shadow: 0px 10px 20px 0px #02a1c3;
     animation: meteoring 6s infinite linear;
     &::before {
        width: 2px;   height: 2px;
        content: "";
        position: absolute;  bottom: 0px;  left: 0px;
        border-radius: 50%;         background: #00d0ff;
        box-shadow: 0px 0px 12px 5px #00d0ff;
     }
  }

}

@keyframes meteoring {
    0% {
        opacity: 0;
        top: -60%;
    }
    25% {
        opacity: 0.5;
        top: -40%;
    }
    50% {
        opacity: 0.8;
        top: -20%;
    }
    75% {
        opacity: 1;
        top: 10%;
    }
    100% {
        opacity: 0;
        top: 60%;
    }
}
</style>
